<template>

    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" @click.prevent="decrementProduct">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" :value="productAmount" @input="$emit('update:productAmount', $event.target.value)"> 

        <button type="button" aria-label="Добавить один товар" @click.prevent="incrementProduct">
            <svg width="12" height="12" fill="currentColor" >
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>         

</template>

<script>
export default {
    props: ['productAmount'],
    methods: {
        incrementProduct() {
            this.$emit('update:productAmount', this.productAmount += 1)
        },
         decrementProduct() {
            if (this.productAmount > 1) {
                this.$emit('update:productAmount', this.productAmount -= 1)
            }
        },
    },
    
}
</script>