<template>
    <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <button class="pagination__link pagination__link--arrow pagination__link--disabled" aria-label="Предыдущая страница" @click.prevent="paginate(page -1)" :disabled="page <= 1">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </button>
          </li>
          <li class="pagination__item" v-for="pageNumber of pages" :key="pageNumber">
            <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
            <button class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница" @click.prevent="paginate(page + 1)" :disabled="page === pages">
                <svg width="8" height="14" fill="currentColor">
                 <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </button>
          </li>
        </ul>
</template>

<script>
export default {
    model: {
        prop: 'page',
        event: 'paginate',
    },
    props: ['page', 'count', 'perPage'],
    computed: {
        pages() {
            return Math.ceil(this.count / this.perPage);
        }
    },
    methods: {
        paginate(page) {
            this.$emit('paginate', page);
        }
    }
}
</script>